import Vue from 'vue'
import Router from 'vue-router'

import bill from '@/components/bill'
import loading from '@/components/loading'
import footer from '@/components/footer' //

import index from '@/pages/index' //

import pay from '@/pages/pay/pay' // 
import payEnquiry from '@/pages/pay/payEnquiry' //

import myHouse from '@/pages/house/myHouse'   //
import addHouse from '@/pages/house/addHouse' //
import bindHouse from '@/pages/house/bindHouse'  //

import suggest from '@/pages/suggest/index'   //

import news from '@/pages/news/index'        //



Vue.use(Router)

const router= new Router({
  routes: [
    {
      path:'/',
      name:'index',
      component:index,
      meta:{
        title:''
      },
    },
    {
      path:'/pay',
      name:'pay',
      component:pay,
      meta:{
        title:''
      }
    },  
    {
      path:'/payEnquiry',
      name:'payEnquiry',
      component:payEnquiry,
      meta:{
        title:''
      }
    },
    {
      path:'/myHouse',
      name: 'myHouse',
      component:myHouse,
      meta:{
        title: ''
      }
    },
    {
      path: '/addHouse',
      name: 'addHouse',
      component: addHouse,
      meta:{
        title:''
      }
    },
    {
      path: '/bindHouse/:number',
      name: 'bindHouse',
      component: bindHouse,
      meta:{
        title: ''
      }
    },
    {
      path:'/suggest',
      name:'suggest',
      component:suggest,
      meta:{
        title:''
      }
    },
    {
      path:'/news',
      name:'news',
      component:news
    }
  ]
});
//
// to  from next() 
router.beforeEach((to, from, next) => {
    //title
    changeTitle(to.meta.title)
    next();
});

//title
function changeTitle(title) {
    title = title ? title : '';
    window.document.title = title;
};

export default router



